.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

header {
  display: flex;
  justify-content: center;
}

main {
  display: flex;
  justify-content: center;
}

.intputPais {
  color: black;
  width: 70px;
  background-color: white;
  border: solid black 0.5px;
  border-radius: 5px;
  margin-right: 3px;

}
.botonpm {
  color: black;
  border: solid black 0.5px;
  border-radius: 3px;
  margin-right: 1px;
}
.botonCalcular { 
  color: black;
  border: solid black 0.5px;
  border-radius: 5px; 
}

.botonDetalle {
  color: black;
  border: solid black 0.5px;
  border-radius: 5px; 
}
body {
  background-color: lavender;
}

.detalleChances {
  white-space: pre-line;  
}

@media screen and (min-width: 480px){
  .chancesGanar {
    padding: 1%;
    width: 80%;
    border: solid black 3px;
    border-radius: 20px;
    background-color: lavender;
  }

  body {
    /* background-color: darkseagreen; */
    background-color: gainsboro;
  }

  .detalleChances{
    display: flex;
  }
  
  .itemDetalle { 
    padding-right: 30px;
  }
}